@import url("https://fonts.googleapis.com/css2?family=Inknut+Antiqua:wght@300&family=Inter:wght@100;200;400;600;700&display=swap");
/*
Fonts imported:
- Inknut Antiqua
- Inter (weights: 100, 200, 400, 600, 700)
*/
html,
body {
  overflow-x: hidden;
}

body {
  margin: 0;
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-size: 16px;
  position: relative;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
